async function getDelivery() {
  /*
    try {
    const response = await fetch('https://zoxcom.pietycloth.com/backend/api.php?action=getUpsWilaya')

    if (!response.ok) {
      console.error('Erreur lors de la récupération des produits:', response.statusText)
      return
    }

    const result = await response.json()
    wilayas.value = result
    console.log('wilayas.value: ', wilayas.value)
    
  } catch (error) {
    console.error('Une erreur est survenue:', error)
  }
*/
  try {
    const response = await fetch('https://zoxcom.pietycloth.com/backend/api.php?action=getDelivery')

    if (!response.ok) {
      console.error('Erreur lors de la récupération des produits:', response.statusText)
      return
    }

    const result = await response.json()
    for(var i = 0; i < result.data[0].options.length; i++) {
      if (result.data[0].options[i].name === 'yalidine') {
        for(var p = 0; p < result.data[0].options[i].price.length; p++) {
          if(result.data[0].options[i].price[p].isActive) {
            const details = result.data[0].options[i].price[p]
            wilayas.value.push({wilaya_id: p+1, wilaya_name: details.name})
            deliveryFees.value.push({wilaya_id: p+1, tarif: parseFloat(details.home_price), tarif_stopdesk: parseFloat(details.desk_price)})
          }
        }
      }
    }
    
  } catch (error) {
    console.error('Une erreur est survenue:', error)
  }
}

async function setDeliveryPrice() {
  try{
    const wilaya_id = {wilaya_id : selectedWilaya.value.wilaya_id};
    const response2 = await fetch("https://zoxcom.pietycloth.com/backend/api.php?action=getGuepexFees", {
      method: "POST",
      body: JSON.stringify(wilaya_id)
    });

    if (!response2.ok) {
      throw new Error(`HTTP error! Status: ${response2.status}`);
    }

    const response3 = await fetch("https://zoxcom.pietycloth.com/backend/api.php?action=getYalidineFees", {
      method: "POST",
      body: JSON.stringify(wilaya_id)
    });

    if (!response3.ok) {
      throw new Error(`HTTP error! Status: ${response2.status}`);
    }

    
    const result2 = await response2.json();
    const result3 = await response3.json();

    if(result2 && result3){

      selectedFees.value = result2.data.per_commune[communeID.value]
      selectedFees3.value = result3.data.per_commune[communeID.value]

      

      for (var i = 0; i < municipalitys2.value.length; i++) {
        if(municipalitys2.value[i].id === communeID.value) {

          if(municipalitys2.value[i].has_stop_desk) {
            stopDesk.value = true;
            
          }else {
            stopDesk.value = false;
            deliveryIndex.value = 0;
          }
          
          break
        }
        
      }
      for (var ii = 0; ii < municipalitys.value.length; ii++) {
        if (municipalitys.value[ii].id === communeID.value) {
          
            if(municipalitys.value[ii].is_deliverable) {
            home.value = true;
            
          }else {
            home.value = false;
            deliveryIndex.value = 1;
          }
          
          break
        }
        
      }
      
      calculerPrix()
    } else {
      console.error('no result2: ', result2)
    }

    
  } catch (error) {
    console.error(error)
  }

}

async function getCommune(wilaya) {
  selectedWilaya.value = wilaya
  prixFees.value = 0
  order.value.municipality = ""
    try {
    const wilaya_id = {wilaya_id : wilaya.wilaya_id};

    const response = await fetch("https://zoxcom.pietycloth.com/backend/api.php?action=getGuepexCommune", {
      method: "POST",
      body: JSON.stringify(wilaya_id)
    });

    if (!response.ok) {
      throw new Error(`HTTP error! Status: ${response.status}`);
    }

    const response2 = await fetch("https://zoxcom.pietycloth.com/backend/api.php?action=getYalidineCommune", {
      method: "POST",
      body: JSON.stringify(wilaya_id)
    });

    if (!response2.ok) {
      throw new Error(`HTTP error! Status: ${response.status}`);
    }

    
    const result = await response.json();
    const result2 = await response2.json();

    
    
    municipalitys.value = result.data.data;
    municipalitys2.value = result2.data.data;

    console.log('municipalitys2.value: ', municipalitys2.value)

    stopDesk.value = true;

    
  } catch (error) {
    console.error('Une erreur est survenue:', error)
  }
}

