function setCatalog(index) {
  catalogIndex.value = index
  productList.value[indexed.value].image = productList.value[indexed.value].catalog[index].image
}

function setModel(index) {
  productIndex.value = index;

  indexed.value = productList.value.findIndex(item => item.id === id);
  found.value = indexed.value !== -1;

  if (indexed.value !== -1) {
    clearSizeColor();

    const details = productList.value[indexed.value].models[index].details;

    details.forEach((detail, ii) => {
      // Vérifie si la couleur existe déjà
      const colorExists = colors.value.some(item => item.color === detail.color);
      if (!colorExists) {
        colors.value.push({
          id: detail.id,
          modelIndex: index,
          index: ii,
          color: detail.color
        });
        console.log('colors: ', colors.value)
      }

      // Vérifie si la taille existe déjà
      const sizeExists = sizes.value.some(item => item.size === detail.size);
      if (!sizeExists) {
        sizes.value.push({
          id: detail.id,
          modelIndex: index,
          index: ii,
          size: detail.size
        });
      }
    });

    setSize(0);
    setColor(0);
    calculerPrix();
  }
}


async function getProducts() {
  isUpdating.value = true
  try {
    const response = await fetch('https://zoxcom.pietycloth.com/backend/api.php?action=getProducts')

    if (!response.ok) {
      console.error('Erreur lors de la récupération des produits:', response.statusText)
      return
    }

    const result = await response.json()
    for(var i = 0; i < result.data.length; i++) {
      result.data[i].catalog = [{ image: result.data[i].image }, ...result.data[i].catalog];
    }
    productList.value = result.data
    setModel(productIndex.value)

    
  } catch (error) {
    console.error('Une erreur est survenue:', error)
  } finally {
    isUpdating.value = false
  }
}


async function saveOrder() {
  isUpdating.value = true;
            //saveLog.value = 'Saving order, please wait...';
            /*if (!name.value || !phone.value || !qty.value || !country.value || !method.value.name) {
                saveLog.value = 'Please fill in all required fields.';
                return;
            }*/
            const model = productList.value[indexed.value].models[productIndex.value]
            const details = productList.value[indexed.value].models[productIndex.value].details

            if(model.aColor == 1 && !selectedColor.value && details?.length) {
              selectedColor.value = details[0].color;
            } else if (model.aColor != 1) {
              selectedColor.value = '';
            }

            if(model.aSize == 1 && !selectedSize.value && details?.length) {
              selectedSize.value = details[0].size;
            } else if (model.aSize != 1) {
              selectedSize.value = '';
            }
            
            const colorLabel = typeof selectedColor.value === 'object' ? selectedColor.value.color || '' : selectedColor.value || '';
            const sizeLabel = typeof selectedSize.value === 'object' ? selectedSize.value.size || '' : selectedSize.value || '';

            let detail = details.find(d => d.color === colorLabel && d.size === sizeLabel);
            let detailIndex = detail ? detail.id : -1;


            const selectedModel = [{
              
                        name: model.modelName, 
                        image: model.image, 
                        qty: 1,
                        price: parseFloat(model.sell),
                        ref: model.ref,
                        indexD: productIndex.value,
                        details: [model],
                        total: parseFloat(model.sell),
                        idP: productList.value[indexed.value].id,
                        idM: model.id,
                        selected: [{
                            color: selectedColor.value.color, 
                            size: selectedSize.value.size, 
                            index: detailIndex,
                            total: model.sell,
                            qty: 1
                        }]
            }];
            

            var zone1 = false
            var zone2 = false
            var method = ''

            if(deliveryIndex.value === 0) {
              zone1 = true
              zone2 = false
              method = 'guepex'
            } else {
              zone1 = false
              zone2 = true
              method = 'yalidine'
              
            }

            const orderData = {
                time: Math.floor(Date.now() / 1000),
                name: order.value.nom,
                phone: order.value.telephone,
                qty: order.value.qty,
                country: 'algeria',
                method: method,
                select: selectedWilaya.value.wilaya_name,
                deliveryValue: `${prixFees.value} DA`,
                type: zone1 ? "0" : zone2 ? "1" : "",
                zone1: zone1,
                zone2: zone2,
                sZone: order.value.municipality.name,
                mZone: order.value.adresse,
                discount: '',
                discountValue: '',
                note: '',
                total: prixTotal.value,
                selectedProd: selectedModel,
            };
            //saveLog.value = 'Saving order, please wait for response...';
            
            try {
              
              const response = await fetch("https://zoxcom.pietycloth.com/backend/api.php?action=postOrder", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json; charset=UTF-8"
                    },
                    body: JSON.stringify(orderData)
                });

                
                if (!response.ok) {
                  isUpdating.value = false;
                  throw new Error(`HTTP error! Status: ${response.status}`);
                }
                
                const result = await response.json()

                if (result.success) {
                  console.log('result: ', result.data)
                  isUpdating.value = false;
                  router.push(`/thenks/${result.data}`) // ← Redirection avec ID
                } else {
                  isUpdating.value = false;
                  alert(`order not saved correctly ${result.message}`)
                }
            } catch (error) {
              isUpdating.value = false;
              console.error('Error:', error);
              //saveLog.value = `Error: ${error.message}`;
            }

        }


