<template>
  <div class="shop-container">
    
    <main class="bd-grid">
      <div
        v-for="product in modelValue"
        :key="product.id"
        class="card"
        @click="goToProduct(product.id)"
      >
        <div class="card-img">
          <img :src="getImage(product.image)" :alt="product.name" />
        </div>

        <div class="card-content">
          <p class="card-name">{{ product.name }}</p>
          <div class="card-prices">
            <span>{{ getBeforePrice(product) }} DA</span>
          </div>
        </div>

        <a href="#" class="card-icon">
          <ion-icon name="cart-outline"></ion-icon>
        </a>
      </div>
    </main>
  </div>
</template>

<script setup>
import { useRouter } from 'vue-router'
import { defineProps } from 'vue'

const props = defineProps({
  modelValue: {
    type: Array,
    required: true
  }
})

console.log('props: ', props.modelValue)

const router = useRouter()

const getImage = (image) => {
  if (!image) return '/images/placeholder.png'
  return image.startsWith('https') ? image : `https://zoxcom.pietycloth.com${image}`
}

const getBeforePrice = (product) =>
  product.models?.[0]?.original || product.models?.[0]?.sell || ''

//const getNowPrice = (product) => product.models?.[0]?.sell || ''

const goToProduct = (id) => {
  router.push(`/produits/${id}`)
}
